import { makeStyles } from '@material-ui/core'
import Button from 'components/Button'
import Flex from 'components/Flex'
import Input from 'components/Input'
import { useERC721Contract } from 'hooks/useContract'
import React, { useEffect, useState } from 'react'
import KumazaBoss from 'assets/kumazaBoss.jpeg'
import contractAddresses from 'constants/addresses'
import { networkID, openseaUrl } from 'utils/helpers'
import { BigNumber } from 'ethers'
import { bigOne, bigZero, ether, toBN } from 'utils/ethers'
import Anchor from 'components/Anchor'
import Snackbar, { Status } from 'components/Snackbar'

const useStyles = makeStyles((theme) => ({
  container: {
    // width: '100%',
    marginTop: '4rem',
  },
  image: {
    width: '400px',
  },
  hurry: {
    color: 'red',
  },
}))

const price = ether.div(100) // 0.01 ETH

export default function Home() {
  const classes = useStyles()
  const bear = useERC721Contract(contractAddresses[networkID].kumazaNFT)
  const [numToMint, setnumToMint] = useState('0')
  const [remain, setremain] = useState(bigZero)
  const [maxPurchase, setmaxPurchase] = useState(bigZero)
  const [isLoading, setisLoading] = useState(false)
  const [snackbarStatus, setsnackbarStatus] = useState(Status.close)

  useEffect(() => {
    const getKumaZaInfo = async () => {
      if (bear) {
        const data = (await Promise.all([
          bear.MAX_PURCHASE(),
          bear.remaining(),
        ])) as BigNumber[]
        setmaxPurchase(data[0])
        setremain(data[1])
      }
    }
    getKumaZaInfo()
  }, [bear])

  const onMint = async () => {
    try {
      setisLoading(true)
      const bigNumToMint = toBN(numToMint)
      if (bigNumToMint.lt(bigOne)) throw new Error('Cannot mint less than 1')
      else if (bigNumToMint.gt(maxPurchase))
        throw new Error('Exceeded max per account')

      const tx = await bear!.mint(bigNumToMint, {
        value: price.mul(bigNumToMint),
      })
      await tx.wait()

      setsnackbarStatus(Status.success)
    } catch (error) {
      // @ts-ignore
      if (error.code !== 4001) {
        setsnackbarStatus(Status.fail)
      }
      // @ts-ignore
      alert(error.message)
    } finally {
      setisLoading(false)
    }
  }

  return (
    <Flex
      className={classes.container}
      flexDirection="column"
      alignPosition="center"
    >
      <Snackbar
        open={snackbarStatus !== Status.close}
        handleClose={() => setsnackbarStatus(Status.close)}
        status={snackbarStatus}
      />
      <img className={classes.image} src={KumazaBoss} alt="boss" />
      <h3 className={classes.hurry}>
        Hurry! Only {remain.toString()} Kumazas remain
      </h3>
      <Flex alignPosition="center" justifyPosition="center">
        <h4>Max Purchase: {maxPurchase.toString()}</h4>
        <Input
          id="number of tokens to mint"
          value={numToMint}
          onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
            setnumToMint(e.target.value)
          }}
        />
        <Button isLoading={isLoading} onClick={onMint}>
          Mint
        </Button>
      </Flex>
      <Anchor href={openseaUrl}>
        <Button onClick={() => {}}>Opensea</Button>
      </Anchor>
    </Flex>
  )
}
