import React from 'react'
import { makeStyles } from '@material-ui/core'

type FlexPosition =
  | 'initial'
  | 'center'
  | 'flex-end'
  | 'flex-start'
  | 'space-between'
  | 'space-around'

interface IFlexProps {
  flexDirection?: 'row' | 'column'
  alignPosition?: FlexPosition
  justifyPosition?: FlexPosition
  wrap?: boolean
}

interface IFlexContainerProps extends IFlexProps {
  children: React.ReactNode
  className?: string
}

const useStyles = makeStyles(() => ({
  flex: {
    display: 'flex',
    flexDirection: (props: IFlexProps) => props.flexDirection || 'initial',
    alignItems: (props: IFlexProps) => props.alignPosition || 'initial',
    justifyContent: (props: IFlexProps) => props.justifyPosition || 'initial',
    flexWrap: (props: IFlexProps) => (props.wrap ? 'wrap' : 'initial'),
  },
}))

export default function Flex({
  children,
  flexDirection,
  alignPosition,
  justifyPosition,
  className,
  wrap,
}: IFlexContainerProps) {
  const classes = useStyles({
    flexDirection,
    alignPosition,
    justifyPosition,
    wrap,
  } as IFlexProps)
  return <div className={`${classes.flex} ${className || ''}`}>{children}</div>
}
